<template>
  <div class="hello">
    <img class="logo" src="/static/logo.png" alt="logo">
    <p style="fontSize: 0.24rem;color:#ffff51;margin-top: 0.2rem;font-weight: bold">礼品定制服务</p>
    <p style="font-size: 0.14rem;color: white;margin-top: 0.26rem">为企业提供礼品整合营销方案</p>
    <div class="fangan">
      <div class="fangan-item" v-for="i in fangan">{{i}}</div>
    </div>
    <p style="font-size: 0.24rem;color:#ffff51;margin-top: 0.35rem;font-weight: bold">
      员工福利方案
    </p>
    <p style="font-size: 0.14rem;color: white;margin-top: 0.26rem">从福利发放到员工关怀</p>
    <div class="fuli">
      <div class="fangan-item" v-for="i in fangan">{{i}}</div>
    </div>
    <div class="fuli">
      <div class="fangan-item" v-for="i in fangan">{{i}}</div>
    </div>
    <div class="lingqu" @click="$router.push('/submit')">
      点击领取礼品
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      fangan:[
        '促销礼品','福利礼品','商务礼品'
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .logo{
    width: 1.98rem;
    margin-top: 0.7rem;
  }
  .fangan{
    width: 3.1rem;
    height: 0.3rem;
    display: flex;
    justify-content: space-between;
    margin: 0.26rem auto 0;
  }
  .fuli{
    width: 3.1rem;
    height: 0.3rem;
    display: flex;
    justify-content: space-between;
    margin: 0.2rem auto;
  }
  .fangan-item{
    font-size: 0.14rem;
    width: 0.9rem;
    background-color: white;
    border-radius: 0.03rem;
    height: 0.3rem;
    line-height: 0.3rem;
  }
  .lingqu{
    background-image: url("/static/btn-bg-2.png");
    width: 1.5rem;
    height: 0.4rem;
    background-size: cover;
    position: absolute;
    left: 1.13rem;
    top: 5.9rem;
    font-size: 0.15rem;
    color: white;
    line-height: 0.4rem;
  }

</style>



