<template>
  <transition name="fade">
    <div class="toast" v-show="show">
      <div class="mask">
        <div class="pop" v-if="status">
          <img src="/static/success.png" alt="">
          <p>恭喜您已成功提交信息，请联系工作人员领取礼品</p>
          <div class="btn" @click="show=false">
            好的
          </div>
        </div>
        <div class="pop" v-else>
          <img style="width: 0.63rem" src="/static/fail.png" alt="">
          <p>您的企业信息不完善，请填写完成过后再提交信息</p>
          <div class="btn" @click="show=false">
            好的
          </div>
        </div>
        <p style="margin-top: 0.2rem"><img @click="show=false" style="width: 0.2rem" src="/static/close.png" alt=""></p>
      </div>
    </div>
  </transition>
</template>

<script>
    export default {
        name: "toast",
        data(){
          return{
            show:false,
            status:false
          }
        },
        created(){
          let self = this;
          this.bus.$on('toast',function (a) {
            self.show = true;
            self.status = a
          })
        }
    }
</script>

<style scoped>
.mask
{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
}
  .pop
  {
    width: 2.65rem;
    height: 2.3rem;
    background: white;
    border-radius: 5px;
    margin: 1.8rem auto 0;
  }
  .pop img{
    width: 0.8rem;
    margin-top: 0.17rem;
  }
  .pop p{
    width: 1.6rem;
    margin: 0.2rem auto 0;
    font-size: 0.14rem;
  }
  .pop .btn
  {
    background-image: url("/static/btn-bg.png");
    background-size: cover;
    width: 1.6rem;
    height: 0.3rem;
    line-height: 0.3rem;
    color: white;
    margin: 0.28rem auto 0;
  }
</style>



