<template>
    <div class="submit">
      <img class="logo" src="/static/logo.png" alt="logo">
      <p style="
       margin-top: 0.77rem;
      text-align: left;
      color: white;
      font-size: 0.2rem;">请填写企业信息</p>
      <p class="info">
        <span>公司名称：</span>
        <input type="text" v-model="company">
      </p>
      <p class="info">
        <span>您的职务：</span>
        <input type="text" v-model="job">
      </p>
      <p class="info">
        <span>您的姓名：</span>
        <input type="text" v-model="name">
      </p>
      <p class="info">
        <span>您的电话：</span>
        <input type="text" v-model="phone">
      </p>
      <div class="lingqu" @click="submit">
        提交信息
      </div>
    </div>
</template>

<script>
    export default {
        name: "submit",
      data(){
          return{
            company:'',
            job:"",
            name:'',
            phone:''
          }
      },
      methods:{
          submit(){
            let data ={
              company:this.company,
              job:this.job,
              name:this.name,
              phone:this.phone
            };
            let arr = '';
            let status = false;
            for(let i in data){
               if(data[i].length===0){
                 status = false;
                 break
               }else{
                 status = true
               }
            }
            this.bus.$emit('toast',status)
          }
      }
    }
</script>

<style scoped>
  .logo{
    width: 1.98rem;
    margin-top: 0.7rem;
  }
  p
  {
    text-align: left;
    padding: 0 0.25rem;
    color: white;
  }
  p.info
  {
    height: 0.4rem;
    line-height: 0.4rem;
    margin-top: 0.3rem;
  }
  p.info span{
    font-size: 0.15rem;
  }
  p.info input
  {
    display: inline-block;
    box-sizing: border-box;
    max-width: 2.5rem;
    width: 2.45rem;
    outline: none;
    border: none;
    height: 0.4rem;
    line-height: 0.4rem;
    background-color: #b2dbf9;
    border-radius: 3px;
    padding: 0 0.1rem;
  }
  .lingqu{
    background-image: url("/static/btn-bg-2.png");
    width: 1.5rem;
    height: 0.4rem;
    background-size: cover;
    position: absolute;
    left: 1.13rem;
    top: 5.9rem;
    font-size: 0.15rem;
    color: white;
    line-height: 0.4rem;
  }
</style>



